import React from 'react';
import styled from 'styled-components';
import Item2 from './item2'

const ItemList = ({ list }) => {
    
    let items = <h3>loading...</h3>;

    if(list) {
        items = list.map((item, i) => <Item2 key={i} item={item} />);
    }

    return (
        <div>{items}</div>
    );
};

export default ItemList;