import React from 'react';
import ReactDOM from 'react-dom';
import '@atlaskit/css-reset';
import styled from 'styled-components';
import { DragDropContext } from 'react-beautiful-dnd';
import initialData from './initial-date';
import Column from './column';
import LiveSearch from './liveSearch';
import UserApp from './apps/UserApp.jsx';

const Container = styled.div`

`;

class App extends React.Component {
    state = initialData;

    onDragEnd = result => {
        const { destination, source, draggableId } = result;

        if(!destination) {
            return;
        }

        if(
            destination.droppableId === source.droppableId &&
            destination.index === source.index
        ) {
            return;
        }

        const column = this.state.columns[source.droppableId];
        const newItemIds = Array.from(column.itemIds);
        newItemIds.splice(source.index, 1);
        newItemIds.splice(destination.index, 0, draggableId);

        const newColumn = {
            ...column,
            itemIds: newItemIds,
        };

        const newState = {
            ...this.state,
            columns: {
                ...this.state.columns,
                [newColumn.id]: newColumn,
            },
        };

        this.setState(newState);
    };

    render() {
        return (
            <Container>
            <UserApp />
            <LiveSearch />
            {/* <DragDropContext onDragEnd={this.onDragEnd}>
                {this.state.columnOrder.map(columnId => {
                    let column = this.state.columns[columnId];
                    const items = column.itemIds.map(itemId => this.state.items[itemId]);

                    return <Column key={column.id} column={column} items={items} />;
                })}
            </DragDropContext> */}
            </Container>

        );
    }
}

ReactDOM.render(<App />, document.getElementById('root'));
